  letter-spacing: .03em;
  font-family: Jost, sans-serif;
}

.list {
  list-style: none;
}

.link {
  width: fit-content;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.link.current, .link:hover, .link:focus {
  color: var(--primary-text-color);
}

.link.current:after {
  content: "";
  width: 100%;
  height: 4px;
  background: var(--accent-color);
  border-radius: 2px;
  display: block;
  position: absolute;
  bottom: -1px;
  left: 0;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 100px;
  padding-right: 100px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
